/*  start */
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
fieldset,
lengend,
button,
input,
textarea,
th,
td {
    /* table elements  */
    margin: 0;
    padding: 0;
}

ol,
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
}

address,
cite,
dfn,
em,
var {
    font-style: normal;
}
i {
    font-style: normal;
}

/*  */

img,
input {
    border: 0 none;
}

div,
a {
    outline: none
}

a {
    text-decoration: none;
}

.hide {
    display: none
}

html,body {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-overflow-scrolling: iscroll;
    font-family: 'Microsoft YaHei';
}

a,img,button,h4,div{
    text-decoration: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-touch-callout: none;   
    outline: none;
    -webkit-user-select: none;
}
input{
   
}
input,textarea {
     -webkit-appearance: none;
     text-decoration: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-touch-callout: none;   
    outline: none;
}

/*  end */



