}
.rule_2 {
  padding-left: 40px;
}
.default_page {
  text-align: center;
  padding-top: 60px;
  box-sizing: border-box;
}
.default_page img {
    margin-bottom: 20px;
    width: 120px;
}
.default_page p {
    font-size: 14px;
    color: #999;
}
.loading {
  position: relative;
  height: 0;
  bottom: 0;
  overflow: hidden;
  transition: height 1s;
  -moz-transition: height 1s;
  /* Firefox 4 */
  -webkit-transition: height 1s;
  /* Safari  Chrome */
  -o-transition: height 1s;
}
.loading p {
    color: #999;
    text-align: center;
    padding: 10px 0;
    box-sizing: border-box;
}
.loading p img {
      width: 20px;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      -webkit-animation: rotation 1.4s linear infinite;
              animation: rotation 1.4s linear infinite;
      vertical-align: middle;
      margin-right: 5px;
}
.loading p:last-child {
      padding-top: 0px;
}
.loading_show {
  height: 78px;
}
@-webkit-keyframes rotation {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotation {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes rotationTop {
from {
    top: 0;
}
to {
    top: -20px;
}
}
@keyframes rotationTop {
from {
    top: 0;
}
to {
    top: -20px;
}
}
.loading_top {
  position: relative;
  text-align: center;
  overflow: hidden;
  color: #999;
  height: 0;
  background: #eee;
  transition: height 1s;
  -moz-transition: height 1s;
  /* Firefox 4 */
  -webkit-transition: height 1s;
  /* Safari  Chrome */
  -o-transition: height 1s;
}
.loading_top p {
    padding: 3px;
}
.loading_top span {
    width: 90.5px;
    height: 47.5px;
    display: block;
    margin: 0 auto;
}
.bigLoading {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  text-align: center;
  background: rgba(0, 0, 0, 0.2);
  z-index: 3;
}
.bigLoading img {
    width: 45px;
    margin-top: calc(50vh - 22.5px);
    -webkit-animation: rotation 1.8s linear infinite;
            animation: rotation 1.8s linear infinite;
}



